import PropTypes from 'prop-types';
import { styled } from 'styled-components';

const Items = styled.ul`
  padding: 0 5em 0 0;
  margin: 0;
`;

const Item = styled.li`
  --caption-bg: ${({ theme }) => theme.color.ink.base};
  --solid-bg: ${({ theme }) => theme.color.primary.base};
  display: block;
  position: relative;
  height: ${(props) => props.height}px;
  margin-bottom: 32px;
  border: dashed var(--caption-bg);
  border-width: 1px 0;
  list-style: none;

  &::before {
    display: block;
    position: absolute;
    top: 0;
    right: 48px;
    bottom: 0;
    left: 0;
    background-color: var(--solid-bg);
    content: '';
  }

  &::after {
    display: block;
    position: absolute;
    top: 0;
    right: 10px;
    bottom: 0;
    width: 1px;
    background-color: var(--caption-bg);
    content: '';
  }
`;

/** @todo add copy icons to captions */
const Caption = styled.span`
  --arrow-width: 4px;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: calc(100% + var(--arrow-width));
  padding: 2px;
  background-color: var(--caption-bg);
  color: ${({ theme }) => theme.color.dark.ink.base};
  font: normal 10px/12px ${(props) => props.theme.fontFamily.secondaryFont};
  transform: translateY(-50%);
  white-space: nowrap;

  &::before {
    display: block;
    position: absolute;
    top: 50%;
    right: 100%;
    border: var(--arrow-width) solid transparent;
    border-right-color: var(--caption-bg);
    content: '';
    transform: translateY(-50%);
  }
`;

const Spacings = ({ spacings }) => {
  const items = Object.entries(spacings);

  return (
    <Items>
      {items.map(([name, value], i) => (
        <Item key={i} height={value}>
          <Caption>
            {value}px spacing.{name}
          </Caption>
        </Item>
      ))}
    </Items>
  );
};

Spacings.propTypes = {
  spacings: PropTypes.object.isRequired,
};

export default Spacings;
